import React from 'react';
import PropTypes from 'prop-types';
import { Container, Row, Col } from 'reactstrap';

import { Avatar, Title } from './style';

const propTypes = {
  title: PropTypes.string.isRequired,
  avatar: PropTypes.string,
};

const defaultProps = {
  avatar: 'https://robohash.org/my-own-slug.png?size=300x300&set=set1',
};

export default function TitleMovie({ title, avatar }) {
  const movieTitle = title.replace(' ', '&nbsp;');
  return (
    <Container>
      <Row>
        <Col xs="auto" className="bordered orange">
          <Avatar src={avatar} alt={title} />
        </Col>
        <Col className="bordered orange">
          <Title>
            <span>
              {title}
            </span>
          </Title>
        </Col>
      </Row>
    </Container>
  );
}

TitleMovie.propTypes = propTypes;
TitleMovie.defaultProps = defaultProps;